





































import Api from '../../api/api.js'
import Fetch from '../../util/fetch.js'
import wxParse from '../../components/uParse/src/wxParse.vue'
export default {
	components:{
		wxParse
	},
	data() {
		return {
			topical_id: 0,
			article: '<div></div>',
			command:[],
			comment: [],
			imageProp: {
				lazyLoad: false,
				padding: 0,
				mode: 'aspectFit'
			}
		};
	},
	onLoad(options) {
		this.topical_id = options.id;
		if(!this.topical_id) uni.navigateBack();
	},
	mounted() {
		const that = this;
		Fetch.fetchData(Api.TopicDetail,{
			id: that.topical_id
		},(res)=>{
			const data = res.data.data;
			that.article = data.content;
			this.getComment();
			this.getRelated();
		},true);
	},
	methods:{
		getRelated(){
			const that = this;
			Fetch.fetchData(Api.TopicRelated,{
				id: that.topical_id
			},(res)=>{
				const data = res.data.data;
				that.command = data;
			},true);
		},
		getComment(){
			const that = this;
			Fetch.fetchData(Api.CommentList,{
				valueId: that.topical_id,
				typeId: 1,
				size: 5
			},(res)=>{
				const data = res.data.data;
				that.comment = data.data;
			},true);
		}
	}
}
